@import "../../assets/scss/imports";

.badges {
  padding: 0px 30px 20px;

  &_grid {
    display: grid;
    grid-template-columns: repeat(3, 30%);
    justify-content: space-between;
  }

  &_badge {
    @include boxShadow;
    border-radius: 5px;
    margin-bottom: 10px;
    text-align: center;

    &_icon {
      height: 40px;
      width: 40px;
      display: inline-flex;
      border-radius: 50%;
      background-color: $primary;
      color: white;
      justify-content: center;
      justify-self: center;
      align-items: center;
      margin: 10px 0px 12px;
    }

    p {
      font-size: 12px;
    }

    &_link {
        
    }
  }
}
