@import '../../assets/scss/imports';

.dashPod {
    background-color: white;
    border-radius: 5px;
    overflow: hidden;
    @include bottomBorder;
    @include boxShadow;
    margin-bottom: 20px;

    h2 {
        margin-left: 30px;
    }
}

.left {
    grid-column: 1/3;
}

.right {
    grid-column: 3;
}