@import '../../assets/scss/imports';

.search {
    background-color: #ffffff;
    border-radius: 5px;
    overflow: hidden;
    margin: 0px 30px 10px;

    &_box {
        padding: 10px 0px;
        display: grid;
        grid-template-columns: 88% 10%;
        justify-content: space-between;

        &_input {
            height: 30px;
            border-radius: 20px;
            border: solid 1px $darkGray;
            padding: 0px 15px;
        }
    }
}