@import './colours.scss';

h1, h2, h3, h4, h5 {
    font-family: 'Oxygen', sans-serif;
    font-weight: 400;
    color: $primary;
}

p {
    font-family: 'Roboto', sans-serif;
    color: black;
    font-weight: 400;
    font-size: 16px;
}

h2 {
    font-size: 24px;
}

h3 {
    font-size: 18px;
}

@mixin roboto {
    font-family: 'Roboto', sans-serif;
}