@import "../../assets/scss/imports";

.learningJourney {
  margin: 20px 30px;

  &_journey {
    background-image: url(../../assets/images/teams.jpg);
    background-size: cover;
    background-position: center;
    margin: 10px 0px;
    border-radius: 5px;

    h3 {
      padding: 20px 20px 10px;
      margin: 0;
    }

    &_courses {
      display: grid;
      grid-template-columns: repeat(5, auto);
      align-items: center;
      justify-content: space-between;
      padding: 10px 20px;

      &_course {
        background-color: white;
        position: relative;
        width: 90px;
        height: 180px;
        border-radius: 5px;
        text-align: center;
        align-items: start;
        padding: 0px 5px;
        @include boxShadow;

        &.active {
          background-color: $primary;
          width: 120px;
          height: 200px;
          p {
            color: white;
          }

          span {
            top: 52%;

            p {
              color: black;
            }
          }

          button {
            position: absolute;
            bottom: 10px;
            left: 50%;
            transform: translateX(-50%);
            width: 120px;
          }
        }

        p {
          font-size: 14px;
          font-weight: 800;
        }

        span {
          position: absolute;
          top: 70%;
          left: 50%;
          transform: translate(-50%, -50%);
          border-radius: 50%;
        }

        &_circle {
          background-color: $bg;
          height: 60px;
          width: 60px;
          display: flex;
          align-items: center;
          justify-content: center;

          p {
            font-weight: 400;
          }
        }

        &_progress_circle {
          height: 70px;
          width: 70px;
          inset: 0;

          &.zeroPec {
            background: conic-gradient($secondary calc(0 * 1%), #0000 0);
          }

          &.twentyFivePec {
            background: conic-gradient($secondary calc(25 * 1%), #0000 0);
          }

          &.fiftyPec {
            background: conic-gradient($secondary calc(50 * 1%), #0000 0);
          }

          &.seventyFivePec {
            background: conic-gradient($secondary calc(75 * 1%), #0000 0);
          }

          &.oneHundredPec {
            background: conic-gradient($secondary calc(100 * 1%), #0000 0);
          }
        }
      }
    }

    &_links {
      display: grid;
      grid-template-columns: auto 1fr auto;
      align-items: center;
      padding: 10px 20px;

      a {
        text-decoration: none;
        color: $primary;
        font-weight: 600;
      }

      button {
        justify-content: end;
        width: 150px;
        grid-column: 3;
      }
    }
  }

  &_add {
    display: flex;
    justify-content: end;
    margin: 30px 0px;
  }
}
