@import '../../assets/scss/imports';

.quickLinks {
    background-color: #ffffff;
    border-radius: 5px;
    margin: 0px 30px 10px;
    overflow: hidden;

    &_title {

        h3 {
            color: $primary;
            margin: 0;
        }
    }

    &_links {
        padding: 10px 0px 20px;
        
        button {
            margin-right: 10px;
        }
    }
}