$bg: #F0F0F0;
$primary: #143872;
$primaryAlpha: #143872DD;
$secondary: #f68b1f;
$darkGray:#454545;

$text: #000000 ;
$headers: $primary;

.primaryBtn {
    background-color: $primary;
    color: white;
    border-color: $primary;
    transition: all .5s ease;

    &:hover {
        background-color: white;
        color: $primary;
    }
}

.secondaryBtn {
    background-color: white;
    color: $primary;
    border-color: $primary;
    transition: all .5s ease;

    &:hover {
        background-color: $primary;
        color: white;
    }
}

.tertiaryBtn {
    background-color: white;
    color: $primary;
    border-color: white;
    transition: all .5s ease;

    &:hover {
        background-color: $primary;
        color: white;
    }
}