@import '../../assets/scss/imports';

.snapCourses {
    position: relative;
    transition: opacity 1s ease;
    
    &.fade {
        opacity: 0;
    }

    &_image {
        width: 100%;
        background-size: cover;
        background-position: bottom center;
        height: 260px;
        opacity: .7;
        
    }

    &_banner {
        position: absolute;
        bottom: 20px;
        left: 30px;
        right: 30px;
        border-radius: 5px;
        background-color: $primaryAlpha;
        display: grid;
        grid-template-columns: auto 100px;
        align-items: center;
        padding: 10px;

        &_text {
            color: white;
            margin: 5px 10px 5px 0;
        }

        p {
            font-size: 12px;
        }
    }
}
